  margin: 3rem auto;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  justify-items: center;
  grid-gap: 20px;
  gap: 20px;
  grid-row-gap: 20px;
  row-gap: 20px;
}
  
@media only screen and (max-width: 600px) {
